.slick-arrow {
    &::before {}
}

.slick-next {
    left: 90%;

    &::before {
        content: '';
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        width: 19.42px;
        height: 32px;
        display: block;
    }
}

.slick-prev {
    left: 10%;
    z-index: 999;

    &::before {
        content: '';
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        width: 19.42px;
        height: 32px;
        display: block;
    }
}

.SliderContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.FSliderD {
    display: none;
}

@media (min-width:769px) {
    .FSliderD {
        display: block;
    }

    .FSliderM {
        display: none;
    }

}

@media (max-width:1450px) {
    .Product {
        .CardPDP {
            width: 14rem;
            margin: 0px;
        }
    }
}
@media (max-width:1030px){
    
}

@media (min-width:769px) {

    .Product {
        .slick-slide {
            >div {
                width: fit-content;
                margin: 0 auto;
            }
        }

        .slick-track {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin: 0px;
            justify-content: center;
            width: auto !important;
        }
    }

}

@media (max-width:768px) {
    .Product {
        .slick-list {
            padding: 0px 30px;
        }

    }

}